
div[data-v-4]{height:300px;position:relative;margin-bottom:60px
}
header[data-v-4]{border:15px solid green;padding:40px 0;position:absolute;top:300px;width:100%;list("height"):60px
}
@media screen and (min-width: 768px){
header[data-v-4]{list("height"):80px
}
}
@media screen and (min-width: 1224px){
header[data-v-4]{list("height"):100px
}
}
header.is-docked[data-v-4]{margin-top:0;position:fixed;top:0
}



